/* RevenueOverview.css */
.revenue-overview {
    padding: 40px 20px;
    background: #ffffff;
    border-radius: 10px;
    max-width: 600px;
    margin: 0 auto;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .revenue-overview h2 {
    color: #00203F;
    font-size: 2rem;
    margin-bottom: 30px;
    text-align: center;
  }
  
  .revenue-overview p {
    font-size: 1.5rem;
    color: #333;
    text-align: center;
  }
  
  .revenue-overview strong {
    color: #00203F;
  }
  